<template lang="pug">
  .container.auth-container
    .columns.is-vcentered.is-flex-mobile.is-multiline.is-centered
      .column.is-4-desktop.is-12-mobile.is-6-tablet
        h1.title 
          | ðŸ–– {{ t('Welcome to the future!') }}
        .content
          p(v-translate) Let's get started.
          p(v-translate) If you have a UMI wallet, enter your mnemonic phrase.
          p(v-translate) If it's your first time using UMI and you want to earn â€“â€“ create a wallet.

      .column.is-offset-1-desktop.is-narrow-mobile.is-narrow-tablet.is-4-desktop
        b-tabs(
          v-model="activeTab" 
          :animated="false"
        )
          b-tab-item(:label="t('Login')")
            .login-block
              SvgIcon.logo(name="login")
              p.is-size-5.has-text-centered(v-translate) Mnemonic phrase
              .mnemonic-field
                textarea.textarea.has-fixed-size.ym-disable-keys(
                  id="mnemonicField"
                  v-model="mnemonic"
                  @click="focus"
                  @change="hideContent"
                  @keyup.enter="toWallet"
                  @keyup="hideContent"
                  @keydown="hideContent"
                  :class="!mnemonicValid ? 'is-danger' : ''"
                  :placeholder="t('Your mnemonic phrase (BIP39)')"
                  rows="5"
                )
                textarea.fake.textarea.has-fixed-size.ym-disable-keys(
                  v-if="secureMode && !isMobile"
                  readonly
                  v-focus
                  v-model="fakeMnemonic"
                  @click="focus"
                  @change="focus"
                  @keyup.enter="toWallet"
                  @keyup="focus"
                  @keydown="focus"
                  :class="!mnemonicValid ? 'is-danger' : ''"
                  :placeholder="t('Your mnemonic phrase (BIP39)')"
                  rows="5"
                )
                
                a.visible-mode(v-if="!isMobile" @click="changeMode" :class="secureMode ? 'is-ultralight' : 'has-text-link'")
                  SvgIcon.icon(name="eye")
                  
              .buttons.is-centered
                .button.gradient-button(
                  @click="toWallet"
                  :disabled="!mnemonicValid"
                  :class="{'is-loading': entering}"
                  v-translate
                ) Login
                a.button.is-link(
                  @click="toggleTab(1)"
                  v-translate
                ) Create

          b-tab-item(:label="t('Create')")
            .signup-block(v-if="!newMnemonic")
              SvgIcon.logo(name="secure")
              p.is-size-5.has-text-centered(v-translate) First generate a mnemonic phrase
              .level.is-mobile
                .level-left
                  .underline(v-translate) Maximum security. 256 bit encryption
                .level-right.green
                  .icon
                    SvgIcon.icon(name="check-circle")
              .buttons.is-centered
                a.button.is-link(
                  @click="generate"
                  v-translate
                ) Generate

            .new-block(v-else)
              p.is-ultralight(v-translate) Use this mnemonic phrase to log in to your wallet
              textarea.textarea.has-fixed-size.ym-disable-keys.with-new(
                v-model="newMnemonic"
                rows="6"
                v-clipboard:copy="newMnemonic"
                v-clipboard:success="onCopy"
              )
              .danger
                a.is-active.copy-icon.is-ultralight(v-clipboard:copy="newMnemonic" v-clipboard:success="onCopy")
                  SvgIcon.icon(name="content-copy")
                .columns.is-variable.is-1.is-vcentered
                  .column.is-2.has-text-centered
                    SvgIcon(name="danger")
                  .column
                    | {{ t('Please write it down and keep it in a safe place.') }}
                    br
                    | {{ t('A mnemonic phrase CANNOT be recovered!') }}
              
              label.checkbox.is-info
                input(type="checkbox" v-model="approve")
                | {{ t('I confirm that I saved the mnemonic phrase') }}
              
              .buttons.is-centered
                a.gradient-button(
                  :disabled="!approve"
                  @click="approve && toggleTab(0)"
                  v-translate
                ) Create and sign in

</template>

<script>
import { mapActions, mapGetters } from "vuex";
import Header from "@/components/Header";
import SvgIcon from "@/components/common/SvgIcon";
import Footer from "@/components/Footer";
import Socials from "@/components/Socials";
const bip39 = require("bip39");

export default {
  name: "Login",
  components: { Header, SvgIcon, Footer, Socials },
  computed: {
    ...mapGetters(["isMobile"])
  },
  data() {
    return {
      entering: false,
      mnemonic: null,
      secureMode: !this.isMobile,
      fakeMnemonic: null,
      mnemonicValid: true,
      newMnemonic: null,
      approve: false,
      activeTab: 0
    };
  },
  methods: {
    ...mapActions(["login"]),
    toggleTab(tab = 1) {
      this.activeTab = tab;
      this.newMnemonic = null;
    },
    checkMnemonic() {
      if (this.mnemonic.length > 0) {
        this.mnemonic = this.mnemonic.toLowerCase().trim();
      }
      this.mnemonicValid =
        this.mnemonic && this.mnemonic.length > 0
          ? bip39.validateMnemonic(this.mnemonic)
          : false;
    },
    toWallet() {
      this.checkMnemonic();
      if (this.mnemonicValid && this.mnemonic && this.mnemonic.length > 0) {
        this.entering = true;
        this.login(this.mnemonic);
      }
    },
    changeMode() {
      this.secureMode = !this.secureMode;
      this.focus();
    },
    pasteToReal() {
      if (this.fakeMnemonic) console.log(this.fakeMnemonic.indexOf("*"));
      if (this.fakeMnemonic && this.fakeMnemonic.indexOf("*") === -1) {
        this.mnemonic = this.fakeMnemonic;
        this.fakeMnemonic = null;
        this.hideContent();
      }
    },
    focus() {
      if ((this.secureMode && !this.fakeMnemonic) || this.fakeMnemonic === "") {
        this.mnemonic = null;
        this.fakeMnemonic = "|";
      }
      if (this.secureMode) document.getElementById("mnemonicField").focus();
    },
    hideContent() {
      this.focus();
      if (this.mnemonic && this.mnemonic.indexOf(" ") > -1) {
        let words = this.mnemonic
          .split(" ")
          .map(w => (w.length > 1 ? w[0] + "*".repeat(w.length - 1) : w));

        if (words.length > 1) this.fakeMnemonic = words.join(" ") + "|";
        else this.fakeMnemonic = this.mnemonic ? this.mnemonic + "|" : "|";
      } else {
        this.fakeMnemonic = this.mnemonic ? this.mnemonic + "|" : "|";
      }
    },
    generate() {
      this.newMnemonic = bip39.generateMnemonic(256);
    },
    onCopy() {
      this.$buefy.snackbar.open({
        message: this.$translate.text(
          "Copy the mnemonic phrase to the clipboard, save it in a safe place!"
        )
      });
    }
  }
};
</script>

